import m from 'mithril';
import { MdlBase } from './mdl-base';

export class MdlSpinner extends MdlBase {
    oninit(vnode) {
        super.oninit(vnode);
        this.classList.push("mdl-spinner", "mdl-js-spinner");
        vnode.attrs.singlecolor ? this.classList.push("mdl-spinner--single-color") : null;
        vnode.attrs.active ? this.classList.push("is-active") : null;
    }

    view(vnode) {
        return (
            <div
                class={this.classList.join(" ")}
            >
            </div>
        )
    }
}


